.waybill {
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: auto;
  overflow-x: hidden;
}

.waybill-date-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 70px;
}

.waybill-date-label {
  font-family: Roboto, monospace;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 21px;
  color: #005665;
  text-align: center;
}

.waybill-date-date {
  font-family: Roboto, monospace;
  font-style: normal;
  font-weight: 300;
  font-size: 28px;
  line-height: 33px;
  color: #000000;
  margin: 10px 0 10px 25px;
}

.waybill-title {
  font-family: Roboto, monospace;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 28px;
  text-align: center;
  color: #005665;
  margin: 30px 0 20px 0;
}

.waybill-photo {
  width: 280px;
  border-radius: 20px;
}

.field__med {
  margin: 0 0 20px 0;
}